import { useEffect, useContext } from 'react'
import { useHistory } from 'react-router-dom'
import { PlayButton, MainWrapper, Wrapper, TextWrapper, Title } from './style'
import { ConnexionContext } from '../../utils/context'

function BeforeFace2Face() {
  let history = useHistory()
  const { connected } = useContext(ConnexionContext)

  //Permet de rediriger vers l'accueil si une personne tente d'accder  cet cran par l'URL
  useEffect(() => {
    window.scrollTo(0, 0) //Permet de ramener la vue de l'cran au sommet de la page
    if (connected === false) {
      history.push('/')
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [history, connected])

  return (
    <MainWrapper>
      <Title>BRAVO ! C'EST BIENTOT LA FIN !</Title>
      <Wrapper>
        <TextWrapper>
          Flicitations, vous tes venus  bout du 9 points gagnants et du 4 
          la suite. Il ne vous reste plus qu' prendre part au Face  Face.
          <br /> <br />
          Dans cette version revisite du jeu, le but est toujours d'atteindre
          les 12 points, la quantit de points gagns tant degressive en
          fonction du temps mis. <br />
          Mais, ici, vous n'affrontez personne d'autres que vous-mme. En effet,
          il faudra vous "contenter" de faire le moins d'erreurs possible !
          <br /> <br />
          Appuyez sur "FACE A FACE" pour vous lancer dans l'preuve finale !
        </TextWrapper>
        <PlayButton to="/faceaface" $isFullLink>
          {/*Redirige vers le face  face*/}
          FACE A FACE
        </PlayButton>
      </Wrapper>
    </MainWrapper>
  )
}

export default BeforeFace2Face
