import { useEffect, useContext } from 'react'
import { useHistory } from 'react-router-dom'
import {
  PlayButton,
  RulesWrapper,
  Wrapper,
  TextWrapper,
  LeaderboardButton,
  Title,
} from './style'
import { ConnexionContext, SoundContext } from '../../utils/context'
import opening from '../../assets/music/opening.mp3'

function Rules() {
  //Permet de savoir si le son est activ ou non
  const { soundState } = useContext(SoundContext)
  //permet de savoir si le joueur est connect ou non
  const { connected } = useContext(ConnexionContext)

  let history = useHistory()
  //Permet de rediriger vers l'accueil si une personne tente d'accder  cet cran par l'URL
  useEffect(() => {
    window.scrollTo(0, 0) //Permet de ramener la vue de l'cran au sommet de la page
    if (soundState === true) {
      new Audio(opening).play()
    }
    if (connected === false) {
      history.push('/')
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [history, connected])

  return (
    <RulesWrapper>
      <Title>INSTRUCTIONS</Title>
      <Wrapper>
        <TextWrapper>
          Bienvenue dans "Questions pour un cogniticien" ! <br /> <br />
          Je suppose que vous connaissez dj le jeu "Questions pour un
          Champion", ici, le principe est le mme sauf qu'au lieu de rpondre 
          des questions de culture gnrale vous allez rpondre  des questions
          sur l'ENSC, ses lves, ses professeurs et sa vie tudiante. <br />
          <br />
          Les rgles sont simples. Une partie se dcompose en 3 jeux.
          <ul>
            <li>
              Le 9 points gagnants qui consiste  rpondre correctement  des
              questions rapportant plus ou moins de points et qui portent sur
              diffrents thmes.
            </li>
            <br />
            <li>
              Le 4  la suite qui consiste  rpondre de manire correcte  4
              questions d'affile sur un thme choisi. Toute mauvaise rponse
              ramne le compteur de points  0. De plus, vous tes limit dans
              le temps. Le score retenu sera le meilleur effectu.
            </li>
            <br />
            <li>
              Le Face  Face qui consiste  une suite de questions o le temps
              est prcieux. En effet, plus vous mettez de temps  rpondre,
              moins vous gagnerez de points. Attention, si vous mettez trop de
              temps cela comptera comme une erreur et la question changera ! Le
              but est d'arriver  12 points le plus vite possible.
            </li>
          </ul>
          Votre score final dpend du temps total que vous avez mis, de votre
          nombre d'erreurs ainsi que de votre score au 4  la suite. Vous pouvez
          consulter le tableau des scores en cliquant sur le bouton
          "LEADERBOARD" ci-dessous ou, lancer une partie en cliquant sur
          "JOUER".
        </TextWrapper>
        <PlayButton to="/9gagnants" $isFullLink>
          {/*Redirige vers la premire preuve*/}
          JOUER
        </PlayButton>
        <LeaderboardButton to="/leaderboard" $isFullLink>
          {/*Redirige vers le leaderboard*/}
          LEADERBOARD
        </LeaderboardButton>
      </Wrapper>
    </RulesWrapper>
  )
}

export default Rules
