import { useEffect, useContext, useState } from 'react'
import { useHistory } from 'react-router-dom'
import { ConnexionContext, ThemeContext } from '../../utils/context'

import Theme from '../../components/Theme/index'
import { Suite4Wrapper, Title, Themes, OverlayTheme } from './style'

function Suite4Theme() {
  let history = useHistory()

  //Contexte qui permet de savoir l'tat de l'utilisateur
  const { connected } = useContext(ConnexionContext)

  //Contexte pour stocker le thme choisi par l'utilisateur
  const { choosenTheme } = useContext(ThemeContext)

  //State qui permet de changer le thme
  const [actualTheme, setActual] = useState('')

  //Redirige le joueur si il n'est pas connect et tente d'accder  la page par l'url
  useEffect(() => {
    window.scrollTo(0, 0)
    if (connected === false) {
      history.push('/')
    }
  }, [history, connected])

  //Change le thme actuel par celui stock dans le contexte
  function ChangeTheme() {
    setActual(choosenTheme[0])
  }

  return (
    <Suite4Wrapper>
      <Title>Veuillez choisir un thme</Title>
      {actualTheme === '' ? (
        <Themes>
          {/* Lorsque l'on clique sur un thme, le composant Theme change le thme
          dans le contexte et on appelle la fonction ChangeTheme pour rcuprer
          cette information du contexte */}

          <div
            onClick={() => {
              ChangeTheme()
            }}
          >
            <Theme theme={'Ecole'} />
          </div>
          <div
            onClick={() => {
              ChangeTheme()
            }}
          >
            <Theme theme={'Enseignants'} />
          </div>
          <div
            onClick={() => {
              ChangeTheme()
            }}
          >
            <Theme theme={'Eleve'} />
          </div>
          <div
            onClick={() => {
              ChangeTheme()
            }}
          >
            <Theme theme={'Vie Etudiante'} />
          </div>
        </Themes>
      ) : (
        <OverlayTheme>
          {/* Affiche un message contenant le thme choisi */}
          C'est parti pour un 4  la suite sur le thme : {choosenTheme}
        </OverlayTheme>
      )}
    </Suite4Wrapper>
  )
}

export default Suite4Theme
